/* Global Styles */
body {
    margin: 0;
    font-family: "Poppins", sans-serif;
    background-color: #f0f4f8;
    color: #333;
  }
  
  a {
    text-decoration: none;
    color: #007bff;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  h1, h2, h3, h4, h5, h6 {
    margin: 0;
  }
  
  p {
    margin: 0;
    line-height: 1.6;
  }
  
  /* Login Container */
  .login-container {
    display: flex;
    justify-content: center;
    align-items: flex-start; /* Align content to the top */
    min-height: 50vh; /* Reduced height */
    background-color: #f0f4f8;
    padding: 1rem;
  }
  
  .login-content {
    display: flex;
    max-width: 1000px;
    width: 100%;
    background: white;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
    flex-direction: row;
  }
  
  .login-left {
    padding: 1.5rem; /* Reduced padding */
    width: 50%;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center; /* Centers content vertically */
    align-items: center; /* Centers content horizontally */
    text-align: center; /* Centers text inside the section */
  }
  
  .login-left .logo {
    height: 125px;
    width: auto;
    margin-bottom: 1.5rem; /* Adds space below the logo */
  }
  
  h2 {
    font-size: 1.8rem;
    font-weight: 600;
    color: #333;
    margin-bottom: 1rem;
  }
  
  p {
    font-size: 1rem;
    color: #666;
    margin-bottom: 1.5rem;
  }
  
  .login-form {
    margin-top: 1rem;
  }
  
  .form-group {
    margin-bottom: 1.25rem; /* Reduced margin */
  }
  
  label {
    display: block;
    font-size: 0.95rem; /* Reduced font size */
    font-weight: 500;
    margin-bottom: 0.5rem;
    color: #333;
    text-align: left; /* Ensure labels are aligned to the left */
  }
  
  .form-container {
    border: 2px solid #ccc; /* Adds a border around the form */
    padding: 20px;
    border-radius: 8px;
    width: 400px;
    margin: 0 auto;
  }
  
  input {
    font-family: "Poppins", sans-serif;
    width: 93%;
    padding: 0.7rem; /* Reduced padding */
    font-size: 0.95rem; /* Reduced font size */
    border: 1px solid #ddd;
    border-radius: 6px;
    transition: all 0.3s ease;
  }
  
  input[type="checkbox"]:focus {
    border: none;
    outline: none;
    box-shadow: none;
  }
  
  .form-options {
    display: flex;
    justify-content: space-between;
    font-size: 0.9rem;
  }
  
  .check {
    font-size: 0.9rem;
    display: inline-flex;
    align-items: center;
    gap: 5px;
  }
  
  input[type="checkbox"] {
    font-family: unset;
    width: unset;
    padding: unset;
    font-size: unset;
    border: none;
    border-radius: unset;
    transition: none;
  }
  
  .btn-primary {
    width: 100%;
    padding: 0.75rem;
    margin-top: 1rem;
    background: #007bff;
    color: white;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
    font-family: "Poppins", sans-serif;
  }
  
  .btn-primary:hover {
    background: #0056b3;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .para {
    margin-top: 1rem;
  }
  
  .social-login {
    margin-top: 1.5rem;
    text-align: center;
  }
  
  .social-login p {
    margin-bottom: 1rem;
    font-size: 1rem;
    font-weight: 500;
    color: #666;
  }
  
  .btn-secondary {
    font-family: "Poppins", sans-serif;
    width: 100%;
    margin-top: 0.5rem;
    padding: 0.75rem;
    font-size: 0.9rem;
    font-weight: 500;
    background: #f4f4f4;
    color: #333;
    border: 1px solid #ddd;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .btn-secondary:hover {
    background: #e4e4e4;
    border-color: #ccc;
  }
  
  /* Login Right Panel */
  .login-right {
    width: 50%;
    background: linear-gradient(135deg, #007bff, #0056b3);
    color: white;
    padding: 1.5rem; /* Reduced padding */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 15px;
  }
  
  .login-right h1 {
    font-size: 1.6rem; /* Reduced font size */
    font-weight: 600;
    margin-bottom: 1rem;
  }
  
  .login-right p {
    font-size: 0.95rem; /* Reduced font size */
    color: #f0f4f8;
    margin-bottom: 2rem;
  }
  
  .sales-report {
    width: 80%;
    height: 200px;
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Mobile Responsiveness */
  @media (max-width: 768px) {
    .login-content {
      flex-direction: column;
      box-shadow: none;
    }
  
    .login-left,
    .login-right {
      width: 100%;
      padding: 1.5rem; /* Reduced padding */
      box-sizing: border-box;
    }
  
    .login-right {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      background: #007bff;
      color: white;
      border-radius: 12px;
    }
  
    .login-right h1 {
      font-size: 1.6rem;
      margin-bottom: 1rem;
    }
  
    .login-right p {
      font-size: 0.95rem;
      line-height: 1.4;
      margin-bottom: 1rem;
      padding: 0 1rem;
    }
  
    .sales-report {
      width: 100%;
      padding: 1rem;
      margin-top: 1rem;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
    }
  }
  
  @media (max-width: 480px) {
    .login-content {
      flex-direction: column;
    }
  
    .login-left,
    .login-right {
      padding: 1.5rem;
    }
  
    .login-right {
      padding: 1.5rem;
    }
  
    h2 {
      font-size: 1.4rem;
      margin-bottom: 1rem;
    }
  
    p {
      font-size: 0.85rem;
      line-height: 1.5;
    }
  
    .btn-primary,
    .btn-secondary {
      font-size: 0.9rem;
      padding: 0.7rem;
    }
  
    .form-group label {
      font-size: 0.9rem;
    }
  
    input {
      padding: 0.7rem;
    }
  
    .sales-report {
      margin-top: 1.5rem;
      padding: 1rem;
      border-radius: 10px;
      box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
    }
  }
  
  /* Media Query for screens smaller than 768px (e.g., tablets, mobile) */
  @media screen and (max-width: 768px) {
    .form-container {
      width: 90%;
      padding: 15px;
      margin-left: 0px;
    }
  }
  
  /* Media Query for very small screens, e.g., mobile phones (max-width: 480px) */
  @media screen and (max-width: 480px) {
    .form-container {
      width: 90%;
    }
  }
  